import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class NgxPageTitleService {

  constructor() { }

  setTitle(title: string, typo: string = '') {
    let title_el = document.querySelector("title");
    if (title) {
      title_el.innerHTML = title;
    } else {
      title_el.innerHTML = typo;
    }
  }
}
